.home {
  min-height: 100vh;
  background: #f8f9fa;
  position: relative;
  overflow: hidden;
  
  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: radial-gradient(
      circle at var(--mouse-x, 50%) var(--mouse-y, 50%),
      rgba(0, 245, 160, 0.08) 0%,
      rgba(0, 217, 245, 0.04) 25%,
      transparent 50%
    );
    pointer-events: none;
    transition: all 0.15s ease;
    transform: translate3d(0, 0, 0);
    will-change: transform;
  }
  
  .hero {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    
    .hero-content {
      max-width: 800px;
      text-align: center;
      
      h1 {
        font-size: 3.5rem;
        font-weight: 600;
        margin-bottom: 1.5rem;
        color: #2d3436;
        
        .highlight {
          background: linear-gradient(120deg, #00f5a0 0%, #00d9f5 100%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          display: inline-block;
        }
      }
      
      .subtitle-container {
        margin-bottom: 2.5rem;
        
        p {
          font-size: 1.25rem;
          color: #636e72;
          line-height: 1.6;
          font-weight: 400;
          margin: 0.3rem 0;
          opacity: 0.9;
          
          &:nth-child(2) {
            opacity: 0.7;
          }
          
          &:nth-child(3) {
            opacity: 0.8;
          }
        }
      }
      
      .cta-buttons {
        display: flex;
        gap: 1.5rem;
        justify-content: center;
        
        button {
          padding: 0.875rem 2rem;
          border-radius: 50px;
          font-size: 1rem;
          font-weight: 500;
          cursor: pointer;
          transition: all 0.2s ease;
          
          &.primary-btn {
            background: linear-gradient(120deg, #00f5a0 0%, #00d9f5 100%);
            color: #fff;
            border: none;
            box-shadow: 0 4px 15px rgba(0, 245, 160, 0.2);
            
            &:hover {
              box-shadow: 0 6px 20px rgba(0, 245, 160, 0.3);
            }
          }
          
          &.ghost-btn {
            background: transparent;
            color: #2d3436;
            border: none;
            
            &:hover {
              color: #00f5a0;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 768px) {
  .home .hero {
    .hero-content {
      h1 {
        font-size: 2.5rem;
      }
      
      .subtitle-container {
        p {
          font-size: 1.1rem;
          margin-bottom: 2rem;
        }
      }
    }
  }
}

.primary-btn {
  position: relative;
  overflow: hidden;
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(120deg, rgba(255,255,255,0.2) 0%, transparent 50%);
    transform: translateX(-100%);
    transition: transform 0.6s ease;
  }
  
  &:hover::before {
    transform: translateX(100%);
  }
}

.highlight {
  position: relative;
  
  &::after {
    content: '';
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(120deg, #00f5a0 0%, #00d9f5 100%);
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.4s ease;
  }
  
  &:hover::after {
    transform: scaleX(1);
    transform-origin: left;
  }
} 